@tailwind base
@tailwind components
@tailwind utilities

// @import "./normalize"
@import "./vars"
@import "./mixins"
@import "./mixinsn"
@import "./fonts"
@import "./sprite_generated"



*
    outline: none !important
// From libs:

// @import "../libs/@splidejs/splide/dist/css/splide-core.min.css"
@import "../libs/swiper/swiper.scss"
// :root

@layer base
    body
        // min-height: var(--appheight)
        background-color: #0d1018
        font-family: $font
        font-size: 15px
        font-weight: 700
        line-height: 1.5
        color: #848098
        -webkit-font-smoothing: antialiased
        -moz-osx-font-smoothing: grayscale
        +md
            background-color: #110f1e
        


.main
    height: var(--appheight)
    position: relative
    overflow: hidden
    display: flex
    flex-direction: column
    justify-content: center
    +sm
        display: block
        min-height: 728px
        height: 1060px
        // @media screen and ( max-height: 600px )
        //     min-height: 115vw
        //     .case-area
        //         min-height: 95vw
        //     .case-content
        //         position: absolute
        //         bottom: 50px
        //         left: 0
        //         right: 0
        // @media screen and ( max-height: 375px )
        //     min-height: 140vw
        //     .case-area
        //         min-height: 135vw
    +l
        height: 780px
.swiper
    height: 100%
    .swiper-buttons
        display: block
        position: absolute
        top: 90px
        left: 50%
        transform: translateX(-50%)
        z-index: 100
        @media screen and ( max-width:  1700px)
            top: 70px
        @media screen and ( max-width:  1366px)
            top: 55px
        @media screen and ( max-width:  1024px)
            top: 45px
        @media screen and ( max-width:  767px)
            top: 15px
        @media screen and ( max-width:  480px)
            top: 35px
    .swiper-text
        font-weight: 400
        font-size: 24px
        line-height: 31px
        color: #ffffff
        cursor: default
        user-select: none
        transition: all .3s ease
        @media screen and ( max-width:  1024px)
            font-size: 20px
            line-height: 24px
        @media screen and ( max-width:  480px)
            font-size: 16px
            line-height: 20px
            white-space: nowrap
            text-align: center
    .swiper-button-prev
        position: relative
        top: 3px
        left: 50%
        transform: translateX(-50%)
        width: 30px
        height: 31px 
        transition: all .3s ease
        @media screen and ( max-width:  767px)
            width: 25px
            height: 26px 
        @media screen and ( max-width:  480px)
            width: 20px
            height: 21px
    .swiper-button-prev:hover 
        transform: translate(-50%, 6px)
        opacity: .6
    .swiper-button-next
        display: block
        position: relative
        bottom: 0px
        left:50%
        transform: translateX(-50%)
        width: 30px
        height: 31px
        transition: all .3s ease
        @media screen and ( max-width:  767px)
            width: 25px
            height: 26px 
        @media screen and ( max-width:  480px)
            width: 20px
            height: 21px
    .swiper-button-next:hover 
        transform: translate(-50%,-6px)
        opacity: .6
    .swiper-slide
        height: 100%
        animation: float 6s ease-in-out infinite
.decor-left
    width: 958px
    border-radius: 50%
    background: rgba(240, 26, 193, 0.37)
    filter: blur(200px)
    position: absolute
    top: -110px
    left: -864px
    z-index: 20
    pointer-events: none
    +md
        width: hmd(480, 958)
        top: hmd(-55, -110)
        left: hmd(-432, -864)
    +xl
        width: 958px
        top: -110px
        left: -864px
    +l
        display: none
    &::after
        content: ''
        display: block
        padding-top: 100%
    
.decor-right
    width: 1145px
    border-radius: 50%
    top: 307px
    right: -608px
    opacity: 0.4
    background: rgba(143, 0, 255, 0.43)
    filter: blur(200px)
    position: absolute
    z-index: 20
    pointer-events: none
    +md
        width: hmd(522, 1045)
        top: hmd(158, 307)
        right: hmd(-304, -608)
    +xl
        width: 1045px
        top: 307px
        right: -608px
    +l
        display: none
    &::after
        content: ''
        display: block
        padding-top: 100%
.maw1440
    max-width: 1440px
    width: 100%
    margin-left: auto
    margin-right: auto
    padding: 0 64px
    @media screen and ( max-width:  1280px)
        padding-left: 20px
        padding-right: 40px
    +sm
        padding-right: 20px
.case-content
    z-index: 20
    +sm
        text-align: center
        padding-top: calc(var(--appheight) - 250px)
        // pointer-events: none
    +sm
        padding: 0
        position: absolute
        left: 0
        right: 0
        bottom: 54px
    h1
        color: #fff
        font-weight: 900
        font-size: 70px
        line-height: 110%
        margin-bottom: 12px
        +sm
            font-size: 42px
        +md
            font-size: hmd(35,70)
        +xl
            font-size: 70px
        +sm
            br
                display: none
        +l
            font-size: 32px
            
    p 
        color: #6C6592
        font-size: 32px
        line-height: 140%
        font-weight: 700
        margin-bottom: 12px
        +md
            font-size: hmd(16,32)
        +xl
            font-size: 32px
        +sm
            display: none
.case-area-wrapper
    +sm
        position: absolute
        left: 0
        right: 0
        top: 0
.case-area
    position: absolute
    right: 0
    top: -50px
    width: 818px
    +sm
        width: 100%
        top: 0
        height: 890px
    +md
        top: hmd(-25, -50)
        width: hmd(409,818)
    +xl
        top: -50px
        width: 818px
    +l
        min-height: 560px
        height: 640px
    &::after
        content: ''
        display: block
        padding-top: 100%
        +sm
            content: none


.case-bg-img
    max-width: none
    position: absolute
    bottom: -163px
    width: 2676px
    right: -731px
    +sm
        bottom: -133px
        width: 1943px
        right: 50%
        transform: translateX(41%)
    +md
        bottom: hmd(-80,-163)
        width: hmd(1338,2676)
        right: hmd(-365,-731)
    +xl
        bottom: -163px
        width: 2676px
        right: -731px
    +l
        // bottom: -14%
        bottom: -100px
        // width: 315%
        // right: -77%
        width: 1354px
        right: 50%
        transform: translateX(41%)
        max-width: none
.case-item-img-wrapper
    width: 100%
    position: absolute
    right: -84px
    top: 42%
    transform: translateY(-50%)
    pointer-events: none
    +sm
        right: 0
        top: auto
        bottom: 1%
        transform: none
    +md
        right: hmd(-42,-84)
    +xl
        right: -84px
    +l
        bottom: 1%
        width: 120%
        left: -10%
    img
        width: 100%
        +sm
            width: 74%
            margin: 0 auto

.case-item-img
    animation: float 6s ease-in-out infinite
@keyframes float
    0%
        transform: translatey(0px)

    50%
        transform: translatey(-20px)

    100%
        transform: translatey(0px)
