.button__l
  display: -webkit-flex
  display: -ms-flexbox
  display: flex
  height: 100%
  position: absolute
  left: 20px
  top: 0
  -webkit-transform: translateX(-100%)
  transform: translateX(-100%)
  padding-right: 20px
  overflow: hidden
  transition: width 0.2s

.button__r
  display: -webkit-flex
  display: -ms-flexbox
  display: flex
  -webkit-flex-direction: row-reverse
  -ms-flex-direction: row-reverse
  flex-direction: row-reverse
  height: 100%
  position: absolute
  right: 20px
  top: 0
  -webkit-transform: translateX(100%)
  transform: translateX(100%)
  padding-left: 20px
  overflow: hidden
  transition: width 0.2s

.button__main
  padding: 11px 60px
  background-color: #cd40a7
  -webkit-clip-path: polygon(20px 0, calc(100% - 20px) 0, 100% 50%, calc(100% - 20px) 100%, 20px 100%, 0 50%)
  clip-path: polygon(20px 0, calc(100% - 20px) 0, 100% 50%, calc(100% - 20px) 100%, 20px 100%, 0 50%)
  transition: background-color .2s
  position: relative
  z-index: 3


@media (max-width: 767px)
  .button__main
    padding: 10px 40px

.button__l, .button__r
  width: 0

.button__l span
  &:nth-child(1)
    width: 80px
    opacity: .2
    margin-right: -60px

  &:nth-child(2)
    width: 60px
    margin-right: -40px
    opacity: 0.4

  &:nth-child(3)
    opacity: 0.6

  -webkit-flex-shrink: 0
  -ms-flex-negative: 0
  flex-shrink: 0
  display: block
  height: 100%
  width: 40px
  -webkit-clip-path: polygon(0 50%, 20px 0, 100% 0, 100% 100%, 20px 100%)
  clip-path: polygon(0 50%, 20px 0, 100% 0, 100% 100%, 20px 100%)
  background-color: #cd40a7
  margin-right: -20px

.button__r span
  -webkit-flex-shrink: 0
  -ms-flex-negative: 0
  flex-shrink: 0
  display: block
  height: 100%
  width: 40px
  -webkit-clip-path: polygon(0 0, calc(100% - 20px) 0, 100% 50%, calc(100% - 20px) 100%, 0 100%)
  clip-path: polygon(0 0, calc(100% - 20px) 0, 100% 50%, calc(100% - 20px) 100%, 0 100%)
  background-color: #cd40a7
  margin-left: -20px

  &:nth-child(1)
    width: 80px
    opacity: .2
    margin-left: -60px

  &:nth-child(2)
    width: 60px
    margin-left: -40px
    opacity: 0.4

  &:nth-child(3)
    opacity: 0.6

.button
  display: inline-block
  font-size: 32px
  font-weight: 900
  line-height: normal
  color: #fff
  text-align: center
  text-transform: uppercase
  text-decoration: none
  vertical-align: top
  position: relative
  margin-top: 22px
  +md
    font-size: hmd(16,32)
  +xl
    font-size: 32px
  +l
    margin-top: 15px
  &:hover
    .button__l, .button__r
      width: 60px

    .button__main, span
      background-color: #cd40a7

