.header
    position: absolute
    left: 0
    right: 0
    top: 0
    z-index: 10
    padding-top: 62px
    z-index: 30
    
    +md
        padding-top: hmd(20, 62)
        
    +xl
        padding-top: 62px
    +l
        padding-top: 48px
    +sm
        .maw1440
            display: flex
            justify-content: center
    .container
        max-width: none
        padding-left: 65px
        padding-right: 65px
        @include xl-max
            padding-left: 20px
            padding-right: 20px
.logo
    +md
        width: hmd(150, 220)
    +xl
        width: 220px
    +l
        width: 150px