// Fluid fonts
// See https://habr.com/post/310186/
// Also check _layout.sass, fix for Safari is required

@function strip-unit($number)
    @if type-of($number) == 'number' and not unitless($number)
        @return $number / ($number * 0 + 1)
    @return $number
@function calcFluidFontSize($f-min, $f-max, $w-min, $w-max, $units: px)
    $f-min: strip-unit($f-min)
    $f-max: strip-unit($f-max)
    $w-min: strip-unit($w-min)
    $w-max: strip-unit($w-max)

    $k: ($f-max - $f-min)/($w-max - $w-min)
    $b: $f-min - $k * $w-min

    $b: $b + $units

    @return calc( #{$k} * 100vw + #{$b} )

@mixin fluidFontSize($f-min, $f-max, $w-min, $w-max, $fallback: false)
    font-size: $f-min
    @media (min-width: $w-min)
        @if ($fallback)
            font-size: $fallback
        font-size: calcFluidFontSize($f-min, $f-max, $w-min, $w-max, px)
    @media (min-width: $w-max)
        font-size: $f-max


// responsive items > 1024 && < 1920
@function hmd($smin, $smax)
    @return calc(#{$smin}px + (#{$smax - $smin}) * ((100vh - 500px) / (970 - 500)))
@function wmd($smin, $smax)
    @return calc(#{$smin}px + (#{$smax - $smin}) * ((100vw - 1024px) / (1700 - 1024)))

// responsive items > 1920
@function hxl($val)
    $a: calc($val / 970)
    $b: $a * 100vh
    @return $b
@function wxl($val)
    $a: calc($val / 1700)
    $b: $a * 100vw
    @return $b